<template>
    <div id="AppNav">
        <div class="content">
            <!-- logo图标 -->
            <div class="logo">
                <a href="/">mbtc.fun</a>
            </div>
            <!-- 导航条 -->
            <div class="nav">
                <router-link  to="/">首页</router-link>
                <router-link  to="/Article">文章</router-link>
                <router-link  to="/Message">留言</router-link>
                <router-link  to="/Links">友链</router-link>
                <router-link  to="/About">关于</router-link>
            </div>
            <!-- sign up -->
            <Faces v-if="userInfo._id"></Faces>
            <div v-else class="user">
                 <el-button 
                    class="sign-up" type="primary" size="mini"
                    @click="handLogin"
                 >登录</el-button>
                 <el-button
                  class="sign-up" type="success" size="mini"

                  @click="handReg"
                  >注册</el-button>
            </div>
        </div>
        <!-- 登入与注册的弹窗 -->
        <Login v-show="ifShowLogin" @close="handleClose"></Login>
    </div>

</template>

<script>

import Login from '@/components/Signup/Login.vue'
import Faces from '@/components/Face/Faces.vue'
import {mapState} from "vuex"
export default {
    data(){
        return{
            ifShowLogin: false,
        }
    },
    components:{
        Login,Faces
    },
    computed: {
        ...mapState(["userInfo"])
    },
    methods:{
        // 登录按钮被点击
        handLogin(){
            this.ifShowLogin = true;
            this.$bus.$emit('change1');
        },
        // 注册按钮被点击
        handReg(){
            this.ifShowLogin = true;
            this.$bus.$emit('change2');
        },
        // 关闭弹窗
        handleClose(){
            this.ifShowLogin = false;
        }
    }
}
</script>

<style lang="less" scoped>
#AppNav{
    width: 100%;
    min-width: 850px;
    height: 50px;
    background-color: #fff;
    box-shadow: 0 0 2px #999;
    .content{
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        max-width: 1310px;
        padding: 0 15px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        // logo
        .logo{
            width: 130px;
            height: 50px;
            a{
                display: block;
                width: 100%;
                height: 100%;
                box-sizing: border-box;
                line-height: 50px;
                letter-spacing: 5px;
                font-family: 'pacifico','sans-serif';
                color: rgba(0, 0, 0, .7);
                font-size: 26px;
            }
        }
        // 导航页
        .nav{
            width: 520px;
            height: 50px;
            // background-color: cyan;
            display: flex;
            justify-content: space-around;
            a{
                height: 100%;
                line-height: 50px;
                padding: 0 25px;
                color: #7777;
                font-weight: bolder;
                border-bottom: 2px solid transparent;
                &.router-link-exact-active{
                    color: #00b7ff;
                    border-bottom: 2px solid #00b7ff;
                }
                &:hover{
                    color: #00b7ff;
                }
            }
            
        }
        // 登录
        .user{
            width: 120px;
            height: 35px;
            
            display: flex;
            justify-content: space-between;
            .sign-up{
                margin-left: 10px;
                margin-top: 10px;
            }
        }

    }
}
</style>