<template>
 <div id="faces">
     <el-popover
       placement="bottom"
       width="80"
       trigger="click"
     >
     <!-- 默认显示 -->
       <div class="img"
            slot="reference"
           :title="userInfo.user"
           :style="{
               backgroundImage: `url(${urlBase}${userInfo.face})`
           }"
        >
        </div>
     <!-- 点击之后显示 -->
     <div class="list">
         <el-button 
            type="info" 
            round size="mini"
            @click="userUpdata"
         >修改信息</el-button>
         <br>
         <el-button 
            type="danger" 
            round size="mini"
            @click="userLoginout"
         >退出登录</el-button>
     </div>
     </el-popover>
     
 </div>
</template>

<script>
// 引入vuex
import {mapState} from "vuex"

 export default {
    
   data () {
     return {
         
        //  判断是否是开发环境  
        urlBase: process.env.NODE_ENV === "development"?"http://localhost:3000/":""
     }
   },
   computed: {
       ...mapState(["userInfo"]),
   },
   methods: {
    //    更新用户信息
       userUpdata(){
           this.$router.push("/userinfo")
       },
    // 退出登录
        async userLoginout(){
            let {data}  =  await this.$axios({
                method: "POST",
                url: "/login/logout"
            })
            if(data.code === 0){
                this.$message({
                type: "success",
                duration: 1000,
                message: data.msg
            })
            }
            window.location.href = "/"
            
        }
    }
 }
</script>
<style lang="less" scoped>
#faces{
    overflow: hidden;
    position: relative;
    top: 8px;
    right: 15px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    box-shadow: 0 0 3px #555;
    box-sizing: border-box;
    cursor: pointer;
    .img{
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center center;
    }
}
</style>