<template>
    <div id="AppSide">
        <!-- 个人信息 -->
        <div id="Personal">
            <Personal></Personal>
        </div>
        <!-- 吸附盒子 -->
        <div class="stick" :class="{'fixed': iffixed}">
            <!-- 热门文章 -->
            <div id="Article">
                <HotArticle/>
            </div>
            <!-- 最近访客 -->
            <div id="Visitor">
                <Visitor/>
            </div>
        </div>
        

    </div>
</template>

<script>
import Personal from './Personal/Personal.vue';
import HotArticle from './HotArticle/HotArticle.vue';
import Visitor from './Visitor/Visitor.vue';
export default {
    name: 'AppSide',
    components: {
        Personal,HotArticle,Visitor
    },
    directives: {  },
    data() {
        return {
            iffixed: false,
        };
    },
    methods: {

        // 滚动吸顶函数
        fixed(){
            window.addEventListener("scroll",()=>{
                let scrollTop = document.documentElement.scrollTop;
                if( scrollTop >= 230){
                    // 要吸顶
                    this.iffixed = true;
                }else{
                    // 不用吸顶
                    this.iffixed = false;
                }
            })
        }
    },
    mounted() {
        this.fixed();
    }
};
</script>

<style lang="less" scoped>
#AppSide{
    width: 270px;
    .stick{
        width: 270px;
        margin-top: 15px;
        &.fixed{
                position: fixed;
                top: 55px;
            }
        .Article{
            width: 270px;
        }
    }
}
</style>