import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userInfo: {}
  },
  mutations: {
    // 登录成功，改变vuex
    login(state,userInfo){
      state.userInfo = userInfo
    },
    // 推出登录 
    logout(state){
      state.userInfo = {}
    }
  },
  actions: {
  },
  modules: {
  }
})
