<template>
 <div id="Essay">
     <article class="essay">
        <h2>ARTICLE</h2>
        <p class="des">热门文章推荐。
        <router-link to="/article">更多文章</router-link>
          请访问文章页&gt;.&lt;
        </p>
        <div class="content">
          <div class="left">
            <div class="art" v-if="hotArticle[0]">
              <img src="../../assets/img/article/0.jpg" alt="">
              <p class="title">{{hotArticle[0].title}}</p>
              <div class="cover">
                <router-link :to="'/Article/'+hotArticle[0]._id">
                  <p>Tag: {{hotArticle[0].title}}</p>
                  <p>View More</p>
                </router-link>
              </div>
            </div>
            <div class="art" v-if="hotArticle[1]">
              <img src="../../assets/img/article/1.jpg" alt="">
              <p class="title">{{hotArticle[1].title}}</p>
              <div class="cover">
                <router-link :to="'/Article/'+hotArticle[1]._id">
                  <p>Tag: {{hotArticle[1].title}}</p>
                  <p>View More</p>
                </router-link>
              </div>
            </div>
          </div>
          <div class="right">
            <div class="art" v-if="hotArticle[2]">
              <img src="../../assets/img/article/01.jpg" alt="">
              <p class="title">{{hotArticle[2].title}}</p>
              <div class="cover">
                <router-link :to="'/Article/'+hotArticle[2]._id">
                  <p>Tag: {{hotArticle[2].title}}</p>
                  <p>View More</p>
                </router-link>
              </div>
            </div>
          </div>
    </div>
      </article>
 </div>
</template>

<script>
 export default {
   data () {
     return {
      hotArticle: [],
     }
   },
   components: {

   },
   methods: {
    async getArticle(){
      let res = await this.$axios.get("/art/hot")
      this.hotArticle = res.data.data

    
    }
   },
    created(){
    this.getArticle()

    this.$bus.$on("articleUpdata", () => {
      this.getArticle()
    })
  }
 }
</script>

<style lang="less" scoped>
    #Essay{
        >article{
            padding: 15px;
            margin-bottom: 15px;
            background-color: #fff;
            font-family: 'Quicksand','Microsoft YaHei',sans-serif;  

            >h2{
                font-family: 'Quicksand','sans-serif';
                font-size: 30px;
                letter-spacing: 3px;
                margin-bottom: 15px;
            }
            p.des{
                padding-bottom: 20px;
                border-bottom: 1px solid #ddd;
                margin-bottom: 20px;
                text-indent: 2em;
                color: #444;
                font-size: 14px;
                letter-spacing: 4px;
                line-height: 30px;
                a{
                    text-decoration: underline;
                    color: #aaa;
                }
            }
            div.content{
              display: flex;
              .left,.right{
                img{
                  display: block;
                  width: 100%;
                }
                .art{
                  position: relative;
                  overflow: hidden;
                  p.title{
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    height: 30px;
                    width: 100%;
                    background-color: rgba(0, 0, 0, .1);
                    font-weight: 700;
                    line-height: 30px;
                    color: #fff;
                    text-align: center;
                    transition: bottom .3s;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                  }
                  div.cover{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 2;
                    a{
                      display: block;
                      width: 100%;
                      height: 100%;
                      background-color: rgba(0, 0, 0, .5);
                      opacity: 0;
                      transition: opacity .3s;
                      p{
                        position: absolute;
                        transition: all .3;
                        font-weight: 700;
                      }
                      p:nth-child(1){
                        top: 20%;
                        width: 100%;
                        text-align: center;
                        color: #fff;
                        font-size: 22px;
                        transition: uppercase;
                      }
                      p:nth-child(2){
                        left: 50%;
                        bottom: 20%;
                        width: 30%;
                        height: 32px;
                        border-radius: 16px;
                        text-align: center;
                        line-height: 32px;
                        transform: translateX(-50%);
                        color: #000;
                        background-color: #bfe2e6;
                      }
                    }
                  }
                  &:hover{
                    p.title{
                      bottom: -30px;
                    }
                    div.cover a{
                      opacity: 1;
                      p:nth-child(1){
                        top: 32%;
                      }
                      p:nth-child(1){
                        bottom: 32%;
                      }
                    }
                  }
                }
              }
              div.left{
                width: 50%;
                box-sizing: border-box;
                padding-right: 1px;
                &:nth-child(1){
                  box-sizing: border-box;
                  padding-bottom: 1px;
                }
                &:nth-child(2){
                  box-sizing: border-box;
                  padding-top: 1px;
                }
              }
              div.right{
                width: 50%;
                box-sizing: border-box;
                padding-left: 1px;
              }
            }
        }
    }
</style>
