<template>
    <div id="login" @click="handleClose">
        <div class="content" @click.stop>
            <el-tabs v-model="activeName">
              <el-tab-pane label="立即登录" name="login">
                  <!-- 登录表单 -->
                <el-form
                    :model="LoginForm" 
                    status-icon
                    :rules="Rules"
                    ref="LoginForm" 
                    label-width="100px" 
                >
                    <el-form-item label="用户名" prop="user">
                      <el-input type="text" v-model="LoginForm.user" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="pass">
                      <el-input type="password" v-model="LoginForm.pass" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item>
                      <el-button type="primary" @click="LoginSubimt('LoginForm')">登录</el-button>

                    </el-form-item>
                </el-form>
              </el-tab-pane>
              <el-tab-pane label="注册账号" name="register">
                  <!-- 注册表单 -->
                  <el-form
                    :model="RegisterForm" 
                    status-icon
                    :rules="Rules"
                    ref="RegisterForm" 
                    label-width="100px" 
                >
                    <el-form-item label="用户名" prop="user">
                      <el-input type="text" v-model="RegisterForm.user" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="pass">
                      <el-input type="password" v-model="RegisterForm.pass" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="pass2">
                      <el-input type="password" v-model="RegisterForm.pass2" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item>
                      <el-button type="primary" @click="RegisterSubmit('RegisterForm')">注册</el-button>
                    </el-form-item>
                </el-form>
              </el-tab-pane>
            </el-tabs>

            <!-- 关闭按钮 -->
            <div class="close" @click="handleClose">x</div>
        </div>
    </div>
</template>

<script>

import {mapMutations} from "vuex"


 export default {
    //  监听事件函数
     created() {
         this.$bus.$on("change1", () =>{
             this.activeName = "login"

         });
         this.$bus.$on("change2", () =>{
             this.activeName = "register"

         })
     },
    data () {
     return {
         activeName: "login",
         LoginForm :{
             user: "",
             pass: "",
         },
         RegisterForm :{
             user: "",
             pass: "",
             pass2: ""
         },
         Rules :{
            //  用户名规则
             user: [{
                 validator(rule, value, callback){
                     if( /^[\w\u4e00-\u9fa5]{2,8}$/.test(value) ){
                    
                         callback()
                     }else{
                         callback(new Error("用户名不满足规则"))
                     }
                 },
                 required: true,
                 trigger: 'blur'
             }],
            //  密码规则
             pass: [{
                 validator(rule, value, callback){
                     if(/^[\w\[\]\/\\\-!@#$%^&*()+=|,.<>?:;"'{}]{6,18}$/.test(value) ){
                         callback()
                     }else{
                         callback(new Error("密码不满足规则"))
                     }
                 },
                 required: true,
                 trigger: 'blur'
             }],
            //  确认密码规则
             pass2: [{
                 validator: (rule, value, callback) => {
                     if( value === this.RegisterForm.pass){
                         callback()
                     }else{
                         callback("两次密码不一致")
                     }
                     
                 },
                 required: true,
                 trigger: 'blur'
             }]
         },
     }
   },
   methods: {
    //    vuex
    ...mapMutations(["login"]),
    //    登录验证
    LoginSubimt(form){
         
         this.$refs[form].validate( async valid =>{
                if(valid){
                    // 验证通过
                    let {data} = await this.$axios({
                        method: "POST",
                        url: "/login",
                        data: {
                            user: this.LoginForm.user,
                            pass: this.LoginForm.pass
                        }
                    })
                // 登录失败
                if(data.code !== 0){
                    return this.$message({
                        type: "error",
                        duration: 1000,
                        message: data.msg
                    }
                    )
                }

                // 登录成功
                this.$message({
                    type: "success",
                    duration: 1000,
                    message: "登陆成功"
                })
                /*清空表单内容*/
                this.$refs[form].resetFields();
                // 关闭弹窗
                this.handleClose();
                // 更新vuex的数据
                this.login(data.data)

                }else{
                    // 验证不通过
                    return false
                }
           })
    },
    //    注册验证
    RegisterSubmit(form){
        this.$refs[form].validate(async valid =>{
                if(valid){
                    // 验证通过
                    let {data} = await this.$axios({
                        method: "POST",
                        url: "/reg",
                        data: {
                            user: this.RegisterForm.user,
                            pass: this.RegisterForm.pass
                        }
                    })
                    // 判断注册是否成功
                    if(data.code !== 0){
                        // 注册失败，提示
                        return this.$message.error(data.msg);
                    }
                    // 注册成功
                    this.$refs[form].resetFields();/*清空表单内容*/
                    // 跳转到登录弹窗
                    this.activeName = "login"
                    // 注册成功
                    this.$message.success("注册账号成功")
                    
                }else{
                    // 验证不通过
                    return false
                }
           })
    },
    // 弹窗关闭按钮
    handleClose(){
        this.$emit('close');
    }
   },
   
 }
</script>

<style lang="less" scoped>
    #login{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 99;
        background-color: rgba(0, 0, 0, .5);
        user-select: none;
        .content{
            position: absolute;
            box-sizing: border-box;
            width: 500px;
            height: 300px;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            background-color: white;
            padding: 0 15px;
            .close{
                position: absolute;
                color: #aaa;
                box-sizing: border-box;
                top: 9px;
                right: 20px;
                width: 20px;
                height: 20px;
                font-size: 12px;
                border: 1px solid #aaa;
                text-align: center;
                border-radius: 50%;
                cursor: pointer;
                &:hover{
                    // box-shadow: 0 0 2px  #000;
                    border-radius: rgba(0, 195, 255, .7);
                    background-color:  rgba(0, 195, 255, .7);
                }
            }
        }
    }
</style>
