<script>

import AppNav from "@/components/AppNav/AppNav.vue"
import AppSide from "@/components/AppSide/AppSide.vue"

import {mapMutations} from "vuex"

export default {
  name: 'app',
  components: {
    AppNav,AppSide
  },
  methods: {
    ...mapMutations(["login"])
  },
  async mounted(){
    // 检测是否有session信息
    let {data} = await this.$axios({
      method: "POST",
      url: "./login/check"
    })
    // 更新vuex
    data.code === 0 && this.login(data.data)
  },
}
