<template>
 <div id="Contact">
     <article class="contact">
         <h2>CONTACT ME</h2>
         <p class="des">有任何问题请联系我吧，收到消息后会给您回复邮件的哦♪(^∇^*)</p>
         <el-form
            :model="ruleForm"
            :rules="rules"
            status-icon
            ref="ruleForm"
            class="demo-ruleForm">
            <el-form-item label="Your Name" prop="name">
              <el-input
                  type="text"
                  v-model="ruleForm.name"
              ></el-input>
            </el-form-item>
            <el-form-item label="Your Email" prop="email">
              <el-input
                  type="text"
                  v-model="ruleForm.email"
              ></el-input>
            </el-form-item>
            <el-form-item label="Subject" prop="subject">
              <el-input
                  type="text"
                  v-model="ruleForm.subject"
              ></el-input>
            </el-form-item>
            <el-form-item label="Your Message" prop="message">
              <el-input
                  type="text"
                  v-model="ruleForm.message"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                  type="primary"
                  @click="sendSubmit"
              >Send
              </el-button>
            </el-form-item>
        </el-form>
     </article>
 </div>
</template>

<script>
 export default {
   data () {
     return {
         ruleForm: {
             name: "",
             email: "",
             subject: "",
             message: "",
         },
         rules: {
            name: [
              {required: true, trigger: "blur"}
            ],
            email: [
              {type: "email", required: true, trigger: "blur"}
            ],
            subject: [
              {required: true, trigger: "blur"}
              ],
            message: [
              {required: true, trigger: "blur"}
            ]
        }
     }
     
   },
   methods: {
      async sendSubmit() {
                //    通过所有表单验证,发送请求给后端
                  let {data} = await  this.$axios({
                  method: "POST",
                  url: "/contact",
                  data: {
                    name: this.ruleForm.name,
                    email: this.ruleForm.email,
                    subject: this.ruleForm.subject,
                    des: this.ruleForm.message
                  }
                })
                if(data.code !== 0){
                  return this.$message.error("参数错误")
                }
                this.$message({
                  type: "success",
                  duration: 1000,
                  message: data.msg,
                  onClose(){
                    // 刷新页面
                    window.location.reload()
                  }
                })
                

              
       }
   }
 }
</script>

<style lang="less" scoped>
    #Contact{
        >article{
            padding: 15px;
            margin-bottom: 15px;
            background-color: #fff;
            font-family: 'Quicksand','Microsoft YaHei','sans-serif';
            >h2{
                font-family: 'Quicksand','sans-serif';
                font-size: 30px;
                letter-spacing: 3px;
                margin-bottom: 15px;
            }
            p.des{
                padding-bottom: 20px;
                border-bottom: 1px solid #ddd;
                margin-bottom: 20px;
                text-indent: 2em;
                color: #444;
                font-size: 14px;
                letter-spacing: 4px;
                line-height: 30px;
            }
            .demo-ruleForm{
                padding: 0 20px;
                .el-button{
                    display: block;
                    width: 100px;
                    height: 40px;
                    margin-top: 35px;
                    border: 0px;
                    border-radius: 20px;
                    box-shadow: 0 0 0 #bbb;
                    background-color: #bfe2e6;
                    font-weight: 700;
                    font-size: 16px;
                    letter-spacing: 2px;
                    font-family: 'Quicksand','sans-serif';
                    color: #000;
                    outline: 0;
                    cursor: pointer;
                    transition: box-shadow .3s;
                    &:hover{
                        box-shadow: 0 3px 5px #bbb;
                    }
                }
            }

        }
    }
</style>